@import './global/styles/Colors.module.css';
@import './global/styles/Fonts.module.css';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100%;
  background: var(--body_background);
}

/* Header Navbar */
.header_navbar_logo {
  width: 50px;
  height: 50px;
  margin-left: 2rem;
}

.header_component {
  display: grid;
  grid-template-columns: 6fr 6fr;
  background: var(--body_background);
  width: 100%;
  height: 80px;
  gap: 1rem;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  margin-right: 2rem;
  position: fixed;
  z-index: 100;
  -webkit-box-shadow: var(--webkit_box_shadow);
  -moz-box-shadow: var(--moz_box_shadow);
  box-shadow: var(--box_shadow);
}

.header_navbar_menu_item {
  list-style: none;
  display: flex;
  width: 100%;
  height: 5rem;
  padding: 2rem;
  gap: 1.5rem;
  justify-content: end;
  align-content: center;
  align-items: center;
  text-align: center;
}

.header_item {
  font-weight: 350;
  font-style: normal;
  font-size: 1.25rem;
  color: var(--color_text);
  font-family: sans-serif;
  text-decoration: none;
  user-select: none;
  align-items: center;
  justify-content: center;
  justify-items: center;
}

.header_item:link,
.header_item:visited {
  text-decoration: none;
  color: var(--link_visited);
}

.header_item:hover,
.header_item:active {
  text-decoration: none;
  color: var(--hover_active);
}

.header_icon_button_item {
  width: 50px;
  height: 50px;
  align-items: center;
  justify-items: center;
  align-content: center;
  color: var(--color_text);
}

.header_icon_button_item_light:link,
.header_icon_button_item_light:visited {
  text-decoration: none;
  color: var(--link_visited);
}

.header_icon_button_item_light:hover,
.header_icon_button_item_light:active {
  text-decoration: none;
  color: var(--hover_active);
}

.header_navbar_icon_button {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  margin-right: 2rem;
  justify-items: center;
}

.header_icon_button_item {
  width: 50px;
  height: 50px;
  align-items: center;
  justify-items: center;
  align-content: center;
  color: var(--color_text);
}

.header_icon_button_item_light:link,
.header_icon_button_item_light:visited {
  text-decoration: none;
  color: var(--link_visited);
}

.header_icon_button_item_light:hover,
.header_icon_button_item_light:active {
  text-decoration: none;
  color: var(--hover_active);
}

.header_logo_and_text {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.25rem;
  font-size: 3rem;
  font-family: 'Roboto Thin', sans-serif;
  text-decoration: none;
  user-select: none;
  color: var(--color_text);
  font-weight: 400;
}

.header_navbar_logo {
  width: 50px;
  height: 50px;
  margin-left: 2rem;
}

.header_item {
  font-weight: 350;
  font-style: normal;
  font-size: 1.25rem;
  color: var(--color_text);
  font-family: sans-serif;
  text-decoration: none;
  user-select: none;
}

.header_item:link,
.header_item:visited {
  text-decoration: none;
  color: var(--link_visited);
}

.header_item:hover,
.header_item:active {
  text-decoration: none;
  color: var(--hover_active);
}

.header_navbar_menu_item {
  display: flex;
  width: 100%;
  height: 5rem;
  padding: 2rem;
  gap: 1.5rem;
  justify-content: end;
  align-content: center;
  align-items: center;
  text-align: center;
}

/* HomePage */
.main_item_product {
  width: 20rem;
  height: 32.5rem;
  border-radius: 0.5rem;
  user-select: none;
  color: var(--color_text);
  -webkit-box-shadow: var(--webkit_box_shadow);
  -moz-box-shadow: var(--moz_box_shadow);
  box-shadow: var(--box_shadow);
}

/* Product Item */
.product_title {
  height: 5.5rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  display: flex;
  flex-direction: row;
  font-family: 'Roboto Light', sans-serif;
}

.product_image_container {
  width: 20rem;
  height: 20rem;
  align-content: center;
  justify-content: center;
  justify-items: center;
}

.product_image {
  width: 20rem;
  height: auto;
  object-fit: cover;
}

.product_description_container {
  height: 8rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  display: flex;
  flex-direction: row;
}

.product_description {
  justify-content: center;
  align-items: start;
  padding: 1rem;
  font-family: 'Roboto Light', sans-serif;
  text-align: justify;
  text-overflow: ellipsis;
}

.product_title_logo {
  width: 25%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.product_title_text {
  width: 50%;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  align-content: center;
}

.product_title_button_share {
  width: 25%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.product_title_text_title {
  text-align: match-parent !important;
}

/* Shimmer Product */
.main_item_product_shimmer_load {
  align-items: center;
  justify-content: center;
  align-content: center;
  justify-items: center;
  width: 20rem;
  height: 32.5rem;
  border-radius: 0.5rem;
  user-select: none;
  background: var(--background_shimmer);
  background-size: 300%;
  background-position-x: 100%;
  animation: shimmer 1s infinite linear;
  -webkit-box-shadow: var(--webkit_box_shadow);
  -moz-box-shadow: var(--moz_box_shadow);
  box-shadow: var(--box_shadow);
}

.product_title_shimmer_load {
  height: 5.5rem;
}

.product_image_shimmer_load {
  display: flex;
  flex-direction: column;
  width: 20em;
  height: 20em;
  align-content: center;
  justify-content: center;
  justify-items: center;
  align-items: center;
  text-align: center;
  -webkit-box-shadow: var(--webkit_box_shadow);
  -moz-box-shadow: var(--moz_box_shadow);
  box-shadow: var(--box_shadow);
  user-select: none;
}

.product_description_shimmer_load {
  width: 20rem;
  height: 8.5rem;
}

.image_shimmer_load {
  width: 75%;
  height: 75%;
  color: var(--image_shimmer_color);
  margin-bottom: -1.5rem;
}

.product_text_shimmer_loading {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  color: var(--color_shimmer_text);
  font-size: 2em;
  margin-bottom: 1em;
}

@keyframes shimmer {
  to {
    background-position-x: 0;
  }
}

/* Artwork Page */
.artwork_page {
  background: var(--body_background);
  width: 100%;
  height: 100%;
  min-height: 100%;
  padding-top: 8rem;
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.main_item_products_artwork_page {
  width: fit-content;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  column-fill: auto;
  padding-top: 1.5%;
  padding-right: 3rem;
  padding-left: 3rem;
  padding-bottom: 15%;
}

/* Graphic Design Page */
.graphic_design_page {
  background: var(--body_background);
  width: 100%;
  height: 100%;
  min-height: 100%;
  padding-top: 8rem;
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.main_item_products_graphic_design_items {
  width: fit-content;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  column-fill: auto;
  padding-top: 1.5%;
  padding-right: 2rem;
  padding-left: 2rem;
  padding-bottom: 15%;
}

/* Print Page */
.main_item_products_print_item {
  width: fit-content;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  column-fill: auto;
  padding-top: 1.5%;
  padding-right: 2rem;
  padding-left: 2rem;
  padding-bottom: 15%;
}

/* Footer */
.footer_div {
  width: 100%;
  height: 1.5rem;
  background: var(--body_background);
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  font-size: 0.8em;
  color: var(--color_text);
  -webkit-box-shadow: var(--webkit_box_shadow);
  -moz-box-shadow: var(--moz_box_shadow);
  box-shadow: var(--box_shadow);
}

.footer_link {
  font-style: normal;
  color: var(--link_visited);
  font-family: sans-serif;
  text-decoration: none;
}

.footer_link:link,
.footer_link:visited {
  text-decoration: none;
  color: var(--link_visited);
}

.footer_link:hover,
.footer_link:active {
  text-decoration: none;
  color: var(--hover_active);
}

.main_footer_text {
  font-size: 0.8em;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.main_container {
  background: var(--body_background);
  width: 100%;
  height: 100%;
  min-height: 100%;
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.image_background_homePage {
  width: 100%;
  height: auto;
  background-color: #212121;
  /*background-image: url("./img/logo-background.png");*/
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
}

.home_image_bg {
  width: 100%;
  height: 100%;
  user-select: none;
  object-fit: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.home_main_text {
  position: absolute;
  top: calc(9vw + 9vh);
  left: calc(2vw + 9vh);
  color: var(--gray-200);
  font-family: 'Concert One', sans-serif;
  font-size: calc(9vw + 4vh);
  font-weight: 500;
  user-select: none;
}

.mobile_btn {
  display: none;
}

.header_item_home {
  display: none;
}

.print_page {
  width: 100%;
  height: 100%;
  min-height: 100%;
  padding-top: 8rem;
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  background: var(--body_background);
}

.about_container_main {
  width: 100%;
  height: 100%;
  padding: 3rem 1.563rem 11% 1.563rem;
  gap: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
  justify-items: center;
  background: var(--body_background);
}

.about_container {
  height: 100%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 0;
}

.left_side_container {
  margin-top: 30%;
  width: 30rem;
  height: 30rem;
  font-size: calc(9vw + 4vh);
  display: block;
  text-align: justify !important;
  text-overflow: ellipsis;
  color: var(--color_text);
}

.about_divider {
  background: var(--body_background);
  width: 30rem;
  height: 0.05rem;
  margin-top: 0.1rem;
}

.about_text_paragraph {
  width: fit-content;
  user-select: none;
  white-space: break-spaces;
  text-overflow: fade;
}

.right_side_container {
  margin-top: 30%;
  width: 100%;
  height: 100%;
}

.about_image {
  width: 30rem;
  height: 30rem;
  pointer-events: none;
  user-select: none;
}

.aboutPageTextPFour {
  font-size: 1.2rem;
}

.aboutPageTextPThree {
  font-size: 1.2rem;
}

.aboutPageTextPTow {
  font-size: 1.2rem;
}

.aboutPageTextPOne {
  font-size: 1.45rem; /*calc(0.8vw + 0.8vh);*/
  font-weight: 700;
}

